<template>
  <div id="index">
    <div class="lucky-wheel">
      <div class="lucky-date">
        <p>2019.5.22-5.24()</p>
        <span @click="record_btn">>></span>
      </div>
      <div class="lucky-title"></div>
      <div class="wheel-main">
        <div class="wheel-pointer-box">
          <div class="wheel-pointer" @click="popover"
               :style="{transform:rotate_angle_pointer,transition:rotate_transition_pointer}"></div>
        </div>
        <div class="wheel">
          <div class="wheel-bg" :style="{transform:rotate_angle,transition:rotate_transition}">
            <div class="prize-list">
              <div class="prize-item" v-for="(item,index) in prize_list" :key="index" v-if="prize_list.length>0">
                <div class="prize-pic">
                  <img :src="config.url + item.image" alt="">
                </div>
                <div class="prize-type">
                  {{item.shopname}}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="lucky-site"></div>
      <img class="qr_img" src="../assets/images/qr1.png" alt="" style="width:5rem;height:5rem">
      <div class="lucky-date">
        <p></p>
      </div>
    </div>
    <!--  -->
    <div @click="end($event)" class="popover" v-if="qr_control">
      <div class="message_box" style="background: transparent">
        <img class="qr_explain" src="../assets/images/qrexplain.png" alt="">
        <img class="qr_img" src="../assets/images/qr1.png" alt="" style="margin:5rem auto 0">
      </div>
    </div>
    <!--  -->
    <div @click="end($event)" class="popover" v-if="personal_control">
      <div class="message_box">
        <div class="message_title">
          <p></p>
          <img src="../assets/images/messagelogo.png" alt="">
        </div>
        <ul class="from">
          <li>
            <span>:</span>
            <div class="input"><input @blur="scrollTop" type="text" v-model.trim="messagefrom.name"></div>
          </li>
          <li>
            <span>:</span>
            <div class="input"><input @blur="scrollTop" type="text" v-model.trim="messagefrom.email"></div>
          </li>
          <li>
            <span>:</span>
            <div class="input"><input @blur="scrollTop" type="text" v-model.trim="messagefrom.company"></div>
          </li>
          <li>
            <span>:</span>
            <div class="input"><input @blur="scrollTop" type="text" v-model.trim="messagefrom.location"></div>
          </li>
          <li>
            <span>:</span>
            <div class="input"><input @blur="scrollTop" type="text" v-model.trim="messagefrom.phone"></div>
          </li>
          <li>
            <span>:</span>
            <div class="input vcodeBox"><input @blur="scrollTop" type="text" v-model.trim="messagefrom.verification">
            </div>
            <div class="vcode" @click="vcodeBtn">{{vcode_time}}</div>
          </li>
        </ul>
        <!--  -->
        <div class="draw_btn" @click="tolottery"></div>
      </div>
    </div>
    <!-- record -->
    <div @click="end($event)" class="popover" v-if="record_control">
      <div class="record_box">
        <img src="../assets/images/record_title.png" alt="">
        <div class="record_text">
          <h3><img src="../assets/images/gift.png" alt=""></h3>
          <ul class="gift_list">
            <li v-for="item in gift_list">
              <div>
                <p>{{item[0].shopname}}</p>
                <p>2019.5.23</p>
              </div>
              <span @click="to_conversion(item[0].shop_id)" v-if="item[0].status ==='0'">>></span>
              <span style="background: #414141;color:#fff;" v-else></span>
            </li>
          </ul>
          <!--  -->
          <div class="close" @click="close">
            <img src="../assets/images/close.png" alt="">
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div @click="end($event)" class="popover" v-if="toast_control">
      <div class="message_box toast" style="background: transparent">
        <img src="../assets/images/h5_03.png" alt="">
        <div>
          <p></p>
          <img :src="config.url + prize_list[index].image" alt="">
          <p v-if="prize_list.length>0">{{prize_list[index].shopname}}</p>
        </div>
        <img @click="conversion" class="toastbtn" src="../assets/images/acknow.png" alt="">
        <span @click="close"></span>
      </div>
    </div>
    <!--  -->
    <div @click="end($event)" class="popover" v-if="repeat_control">
      <div class="message_box repeat" style="background: transparent">
        <img src="../assets/images/repeat.png" alt="">
        <span @click="close"></span>
      </div>
    </div>
    <!--  -->
    <div @click="end($event)" class="popover" v-if="lottery_code_control">
      <div class="message_box repeat" style="background: transparent">
        <img src="../assets/images/lottery_code.png" alt="">
        <span @click="close"></span>
        <div class="lottery_code_title">
          <p></p>
          <p></p>
          <p>{{prize_list[index].coding}}</p>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="toast-mask" v-if="control_mask"></div>
  </div>
</template>

<script>
  import config from '../config.js'

  let http = 'https://www.opjpg.com';
  export default {
    name: 'Index',
    data() {
      return {
        config: config,
        //--------------------------------------
        prize_list: [],
        // --------------------------------------
        qr_box: false,//  false:
        qr_control: false, // 
        // --------------------------------------
        personal_box: false,//  false:
        personal_control: false, // 
        messagefrom: {
          name: '',
          email: '',
          company: '',
          location: '',
          phone: '',
          verification: '',
        }, // 
        vcode_time: '', // 
        vcode_control: true, // 
        fromrules: false,// 
        // --------------------------------------
        repeat_box: false,//  false:
        repeat_control: false, // 
        // --------------------------------------
        record_control: false, // 
        // --------------------------------------
        toast_control: false, //
        // --------------------------------------
        lottery_code_control: false, // 
        // --------------------------------------
        control_mask: false, //
        // --------------------------------------
        start_rotating_degree: 0, //
        rotate_angle: 0, //
        start_rotating_degree_pointer: 0, //
        rotate_angle_pointer: 0, //
        rotate_transition: "transform 6s ease-in-out", //
        rotate_transition_pointer: "transform 6s ease-in-out", //
        click_flag: false, //
        index: 0, //

        btn: false,

        errList: 1,// 
        openid: '',
        gift_list: [], // 
        giftid: [],//id
      }
    },
    methods: {
      // 
      scrollTop() {
        setTimeout(function () {
          window.scrollTo(0, 0)
        }, 100)
      },
      // 
      getData() {
        this.$axios.post('http://game.iars-expo.com/demo/index/prize')
          .then(res => {
            this.prize_list = res.data.data;
            console.log(this.prize_list);
          });
        this.$axios.post('http://game.iars-expo.com/demo/index/shouye', this.$qs.stringify({
          code: document.cookie.match(/code=(.*?)(;|$)/)[1],
        }))
          .then(res => {
            console.log(res); // -------------------------------
            this.btn = true;
            this.openid = res.data.opednid; // openid
            if (res.data.code === '') {
              this.qr_box = true;
            } else {
              this.qr_box = false;
            }
            if (res.data.mobile) { // 
              this.personal_box = true;
              this.messagefrom.phone = res.data.mobile;
            } else {
              this.personal_box = false;
            }
          });
      },
      // 
      popover() {
        if (!this.btn) return;
        this.btn = false;
        console.log('');
        // ----------------------------------
        // 
        if (!this.qr_box) {
          // 
          this.qr_control = true;
          this.control_mask = true;
          return;
        }
        // 
        if (!this.personal_box) {
          //  
          this.personal_control = true;
          this.control_mask = true;
          return;
        }

        //    
        // 
        this.$axios.post('http://game.iars-expo.com/demo/index/jilu', this.$qs.stringify({
          openid: this.openid,
        }))
          .then(res => {
            console.log('', res);
            this.gift_list = res.data.data;
            // 
            if (res.data.data.length >= 3) {
              alert('')
            } else {
              // id  
              this.giftid = [];
              res.data.date.map((v) => {
                this.giftid.push(v.Id); // id '1,2,3'
              });
              // id
              this.$axios.post('http://game.iars-expo.com/demo/index/choujiang', this.$qs.stringify({
                openid: this.openid,
                shop_id: this.giftid.join(','),
              }))
                .then(res => {
                  console.log(res);
                  if (res.data.data !== 1) {
                    let reg = new RegExp('-', "g");
                    let new_gift = res.data.data.replace(reg, "/").substr(res.data.data.replace(reg, "/").length - 2, 2); // 
                    let now_gift = (new Date).toLocaleDateString().substr((new Date).toLocaleDateString().length - 2, 2); // 
                    if (new_gift < now_gift) {
                      this.repeat_box = false; // 
                      this.click_flag = true
                    } else {
                      this.repeat_box = true; // 
                      this.click_flag = false;
                      this.repeat_control = true;
                      this.control_mask = true;
                    }
                  } else {
                    this.click_flag = true;
                    this.repeat_box = false;
                  }

                  if (this.click_flag) {
                    console.log(this.messagefrom.phone);
                    this.$axios.post('http://game.iars-expo.com/demo/index/pduser', this.$qs.stringify({
                      mobile: this.messagefrom.phone,
                    }))
                      .then(res => {
                        console.log(res);
                        this.prize_list.map((value, index) => {
                          if (value.shopname === res.data.date.yes) {
                            console.log(index);
                            this.index = index;
                            this.rotating();
                          }
                        });
                      })
                  }
                });
            }
          });
      },
      // 
      vcodeBtn() {
        if (!this.vcode_control) return; // 
        this.tolottery_css(new RegExp(/^1[3456789]\d{9}$/).test(this.messagefrom.phone), 5);
        if (!new RegExp(/^1[3456789]\d{9}$/).test(this.messagefrom.phone)) return alert('');

        this.vcode_control = false; // 
        let down = 60;
        this.vcode_time = down + 's';
        let t = setInterval(() => {
          down--;
          this.vcode_time = down + 's';
          if (down < 0) {
            clearInterval(t);
            this.vcode_time = '';
            this.vcode_control = true;
          }
        }, 1000);
        this.$axios.post('http://game.iars-expo.com/demo/index/duanxin', this.$qs.stringify({
          mobile: this.messagefrom.phone,
        }))
          .then(res => {
            console.log(res);
            console.log(res.data.code);
            // if (res.data.code !== 'Success') {
            //   alert('  ');
            // }
          })
      },
      //  
      tolottery_css(reg, index, aa) {
        if (reg) {
          $('.from>li:nth-child(' + index + ')>.input').css({borderBottomColor: '#d9d9d9'});
          return true;
        } else {
          $('.from>li:nth-child(' + index + ')>.input').css({borderBottomColor: 'red'});
          if (this.errList === 1) {
            alert($('.from>li:nth-child(' + index + ')>.input').prev().text().replace(":", "") + '');
            this.errList++;
          }
          return false;
        }
      },
      // 
      tolottery() {
        console.log('');
        this.errList = 1;
        let name = this.tolottery_css(this.messagefrom.name.length > 0, 1);
        let email = this.tolottery_css(new RegExp(/^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/).test(this.messagefrom.email), 2);
        let company = this.tolottery_css(this.messagefrom.company.length > 0, 3);
        let location = this.tolottery_css(this.messagefrom.location.length > 0, 4);
        let phone = this.tolottery_css(new RegExp(/^1[3456789]\d{9}$/).test(this.messagefrom.phone), 5);
        let verification = this.tolottery_css(this.messagefrom.verification.length > 0, 6);
        if (name && email && company && location && location && phone && verification) {
          this.fromrules = true;
        } else {
          this.fromrules = false;
        }
        console.log('', this.fromrules);
        if (this.fromrules) {
          this.$axios.post('http://game.iars-expo.com/demo/index/usersname', this.$qs.stringify({
            usernmae: this.messagefrom.name,
            email: this.messagefrom.email,
            company_name: this.messagefrom.company,
            mobile: this.messagefrom.phone,
            address: this.messagefrom.location,
            verify: this.messagefrom.verification,
            openid: this.openid,
          }))
            .then(res => {
              console.log(res);
              this.btn = true;
              if (res.data.msg === "") {
                this.personal_box = true;
                this.control_mask = false;
                this.personal_control = false;
                this.click_flag = true;
              } else {
                alert(res.data.msg);
              }
            });
        }
      },
      // 
      rotating() {
        console.log('');
        if (!this.click_flag) return; // 
        let type = 0; //  0 =  1 = ()
        let during_time = 5; // 1s
        let result_index = this.index; // prize_list
        let result_angle = [0, -60, -120, -180, -240, -300]; //
        let rand_circle = 6; // 2-3
        this.click_flag = false; // 
        if (type === 0) {
          // 
          // = + + +
          let rotate_angle =
            this.start_rotating_degree +
            rand_circle * 360 +
            result_angle[result_index] -
            this.start_rotating_degree % 360;
          this.start_rotating_degree = rotate_angle;
          this.rotate_angle = "rotate(" + rotate_angle + "deg)";
          //
          // this.rotate_angle_pointer = "rotate("+this.start_rotating_degree_pointer + 360*rand_circle+"deg)";
          // this.start_rotating_degree_pointer =360*rand_circle;
          let that = this;
          // 
          setTimeout(function () {
            that.click_flag = true;

            that.game_over();
          }, during_time * 1000 + 1500); // 
        } else {
          //
        }
      },

      // 
      game_over() {
        console.log('game_over');
        this.toast_control = true;
        this.control_mask = true;
      },
      // 
      record_btn() {
        if (!this.btn) return;
        this.record_control = true;
        this.control_mask = true;
        this.$axios.post('http://game.iars-expo.com/demo/index/jilu', this.$qs.stringify({
          openid: this.openid,
        }))
          .then(res => {
            console.log('', res);
            this.gift_list = res.data.data;
          })
      },
      // 
      to_conversion(id) {
        this.record_control = false;
        this.toast_control = true;
        console.log(this.prize_list);
        this.prize_list.map((value,index)=>{
          console.log(value);
          console.log(value.Id);
          console.log(id);
          if(value.Id == id){
            this.index = index;
          }
        });
        console.log(this.index);
      },
      // 
      close() {
        this.record_control = false;
        this.repeat_control = false;
        this.lottery_code_control = false;
        this.control_mask = false; // 
        this.toast_control = false;
        this.btn = true;
      },
      //  
      conversion() {
        this.toast_control = false;
        this.lottery_code_control = true;
        this.btn = true;
        console.log(this.btn);
      },
      // 
      end(e) {
        if (e.target.className === 'popover') {
          this.qr_control = false; // 
          this.personal_control = false;// 
          this.repeat_control = false; //  
          this.record_control = false; // 
          this.toast_control = false; //  
          this.lottery_code_control = false; //  
          this.control_mask = false; // 
          this.btn = true;
        }
      },
      listener() {  // 
        if (this.qr_control) {
          window.location.href = 'http://www.opjpg.com';
        }
      },
    },
    mounted() {
      //  
      this.getData();
      window.addEventListener('focus', this.listener);
      document.documentElement.addEventListener('touchstart', function (event) {
        if (event.touches.length > 1) {
          event.preventDefault();
        }
      }, false);
    },
    destroyed() {
      window.removeEventListener('focus', this.listener);
    },
    updated() {
      if(this.control_mask){
        var top=$(document).scrollTop();
        $(document).on('scroll.unable',function (e){
          $(document).scrollTop(top);
        })
      }else{
        $(document).unbind("scroll.unable");
      }
    }
  }
</script>

