    border-radius: 0 0 1rem 1rem;
    padding-top: 1rem;
    position: relative;
    z-index: 1;
  }

  .close {
    width: 2.5rem;
    position: absolute;
    bottom: -4.5rem;
    left: 0;
    right: 0;
    margin: auto;
  }

  .close > img {
    width: 100%;
  }

  .record_text > h3 {
    text-align: center;
    font-weight: normal;
    font-size: 1.25rem;
  }

  .record_text > h3 > img {
    height: 1.6rem;
    vertical-align: middle;
    margin-right: 0.3rem;
  }

  .gift_list {
    padding: 1rem 1.5rem;
    height: 9.5rem;
  }

  .gift_list > li {
    margin: 0.5rem 0;
    overflow: hidden;
  }

  .gift_list > li > div {
    float: left;
    width: 70%;
    font-size: 0.875rem;
  }

  .gift_list > li > div > p:nth-child(2) {
    color: #999;
  }

  .gift_list > li > span {
    float: right;
    display: inline-block;
    height: 2rem;
    width: 30%;
    background: #fcca05;
    font-size: 0.875rem;
    text-align: center;
    line-height: 2rem;
    border-radius: 0.3rem;
    color: #ff0000;
  }

  .toast-mask {
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.74);
    z-index: 10000;
    width: 100%;
    height: 100%;
  }

  .toast > img {
    width: 100%;
  }

  .toast > span {
    display: block;
    width: 2rem;
    height: 2rem;
    background: url("~@/assets/images/close_01.png") no-repeat center;
    background-size: 100%;
    margin: 4rem auto 0;
  }

  .toast > div {
    position: absolute;
    top: 0;
    margin-left: 50%;
    transform: translateX(-50%);
    color: #fff;
    padding-top: 5rem;
  }

  .toast > div > p {
    text-align: center;
  }

  .toast > div > p:nth-of-type(2) {
    font-size: 0.875rem;
  }

  .toast > div > img {
    display: block;
    width: 5rem;
    height: 5rem;
    border-radius: 8rem;
    margin: 0.3125rem auto;
    border: 0.25rem solid #fbe8c0;
  }

  .toastbtn {
    position: absolute;
    left: 0;
    right: 0;
    top: 19rem;
    margin: auto;
    width: 75% !important;
  }

  .repeat > img {
    width: 100%;
  }

  .repeat > span {
    display: block;
    width: 2rem;
    height: 2rem;
    background: url("~@/assets/images/close_01.png") no-repeat center;
    background-size: 100%;
    margin: 6.5rem auto 0;
  }

  .lottery_code_title {
    position: absolute;
    top: 10rem;
    width: 100%;
    font-size: 1rem;
  }

  .lottery_code_title > p {
    text-align: center;
  }

  .lottery_code_title > p:nth-child(1) {
    font-weight: bold;
  }

  .lottery_code_title > p:nth-child(2) {
    color: #999;
    font-size: 0.75rem;
    margin: 0.5rem 0 2rem;
  }

  .lottery_code_title > p:nth-child(3) {
    font-weight: bold;
    color: #ff0000;
  }

</style>



